<template>
  <div class="image-container">
    <!--  Swiper -->
    <swiper
      :navigation="true"
      :pagination="{
        clickable: true
      }"
      :mousewheel="{
        invert: false,
        forceToAxis: false,
        releaseOnEdges: true
      }"
      :keyboard="true"
      :loop="true"
      :autoplay="{
        delay: 5000,
        disableOnInteraction: false,
        pauseOnMouseEnter: true
      }"
      :thumbs="{ swiper: thumbsSwiper }" 
      :modules="modules"
      class="mySwiper"
    >
      <swiper-slide>
        <img :src="image" :alt="altText" /> 
      </swiper-slide>

      <swiper-slide>
        <img :src="image" :alt="altText" /> 
      </swiper-slide>

      <swiper-slide>
         <img :src="image" :alt="altText" /> 
      </swiper-slide>
      
    </swiper>

    <!--  Swiper -->
    <swiper
      @swiper="setThumbsSwiper"
      :spaceBetween="10"
      :slidesPerView="5"
      :freeMode="true"
      :watchSlidesProgress="true"
      :modules="modules"
      :breakpoints="{
      '640': {
        slidesPerView: 2,
        spaceBetween: 10,
      },
      '768': {
        slidesPerView: 4,
        spaceBetween: 10,
      },
      '1024': {
        slidesPerView: 5,
        spaceBetween: 10,
      },
    }"
      class="mySwiper-thumbs"
    >
      <swiper-slide>
        <img src="@/assets/3CoffeeCocoaStrawberry()/518081.jpg" alt="" />
      </swiper-slide>
      <swiper-slide>
        <img src="@/assets/4MatchaCitrusPastry()/494007.jpg" alt="" />
      </swiper-slide>
      <swiper-slide>
        <img src="@/assets/6ChestnutPersimmonTea()/482132.jpg" alt="" />
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { ref } from 'vue';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/thumbs';
import 'swiper/css/free-mode';
import { Navigation, Pagination, Mousewheel, Keyboard, Autoplay, FreeMode, Thumbs } from 'swiper/modules';


export default {
  name: 'PageImg',
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    const thumbsSwiper = ref(null);
    

    const setThumbsSwiper = (swiper) => {
      if (swiper) {
        thumbsSwiper.value = swiper;
      }
    };

    return {
      thumbsSwiper,
      setThumbsSwiper,
      modules: [Navigation, Pagination, Mousewheel, Keyboard, Autoplay, Thumbs, FreeMode]
    };
  },
  props:['image','altText']
};
</script>


<style scoped>


/*  */
.image-container {
  width: 500px;
  height: auto;
  aspect-ratio: 1/1;/*  1:1 */
  overflow: hidden; /*  */
}

.mySwiper {
  width: 100%;
  height: 80%; /*  Swiper  80% */
}

.mySwiper-thumbs {
  width: 100%;
  height: 20%; /*  Swiper  20% */
  margin-top: 10px; 
}

.mySwiper-thumbs .swiper-slide {
  margin-right: 0; /*  margin */
  width: auto; /*  */
}

.swiper-slide img {
  width: 100%; /*  100% */
  height: 100%; /*  100% */
  object-fit: cover; /*  */
  cursor: pointer;
}

.swiper-pagination-bullet {
  width: 30px;
  height: 10px;
  border-radius: 15px;
  opacity: 0.6;
  margin: 0.5px;
}

.swiper-pagination-bullet-active {
  opacity: 1;
}

.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  top: 50%; /*  */
  transform: translateY(-50%); /*  */
  z-index: 10; /*  */
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.5); /*  */
  border-radius: 50%; /*  */
}

/*  */
.swiper-button-next {
  right: 10px; /*  10px */
}

.swiper-button-prev {
  left: 10px; /*  10px */
}

@media (max-width: 768px) {
  .image-container {
    width: 200%;
    height: auto; /*  */
    aspect-ratio: 1/1;
  }

  .mySwiper {
    height: 60%; /*  Swiper  */
  }

  .mySwiper-thumbs {
    display: none; /*  Swiper  */
  }

  .swiper-slide img {
    object-fit: contain; /*  */
  }
  
  .swiper-button-next,
  .swiper-button-prev {
    width: 20px; /*  */
    height: 20px;
  }

  .swiper-button-next {
    right: 5px; /*  */
  }

  .swiper-button-prev {
    left: 5px; /*  */
  }
}
</style>
